@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap');

.borderred {
    border: 2px solid red;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

* {
    padding: 0;
    margin: 0 auto;
    font-family: merriweather, sans-serif;
}

:root {
    --lightgray: #DBCFCC;
    --lightblue: #7A9FCD;
    --brown: #512C39;
    --orange: #D68B4D;
    --baseFont: 15px;
    --position--left: 12em;
}

body {
    background-image: url("assets/TilesPortugalCropped (Small).jpg");
    background-repeat: repeat;
    display: flex;
    flex-direction: column;
}

.page {
    background-color: var(--orange);
}

.page--flex {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.block {
    border: transparent;
    border-radius: 0.5em;
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.15);
    padding: 1em;
}

.block__orange {
    background-color: var(--orange);
}

.backgroundcolor {
    background-color: var(--orange);
    margin: 0 auto 1em auto;
    width: 95%;
    padding: 1em;
}


/*css on page where left text and right photo................*/
.left-side {
    padding: 1em;
    margin-bottom: 2em;
    border-radius: 0.5em;
    background-color: var(--orange);
}

.right-side {
    padding: 1em;
    margin-bottom: 2em;
    border-radius: 0.5em;
    background-color: var(--orange);
}

.side__30 {
    width: 30vw;
}

.side__40 {
    width: 40vw;
}

.side__45 {
    width: 45vw;
}

.side__50 {
    width: 50vw;
}

.side__60 {
    width: 60vw;
}

/*photo.....................................................*/
.photo {
    width: 100%;
    padding: 1em;
}

.photo--no-show-on-pc {
    display: none
}

.photo-caption {
    color: black;
    font-size: calc(var(--baseFont) * 0.8);
    margin-left: 1.5em;
}

/*font-size.................................................*/
h1 {
    font-size: calc(var(--baseFont) * 1.6);
    margin_top: 1em;
    margin-bottom: 1em;
}

h2 {
    font-size: calc(var(--baseFont) * 1.4);
    padding-bottom: 0.5em;
}

h3 {
    font-size: calc(var(--baseFont) * 1.2);
    padding-bottom: 0.5em;
}

p {
    font-size: var(--baseFont);
    line-height: 1.5em;
}

li {
    font-size: var(--baseFont);
    line-height: 1.6em;
    margin-left: 1em;
}

.line-through {
    text-decoration-line: line-through;
}

/*anchor tags.................................................*/
a {
    color: black;
    font-size: var(--baseFont);
}

/*margin.....................................................*/
.margin-top1 {
    margin-top: 1em;
}

.margin-top2 {
    margin-top: 2em;
}

.margin-bottom1 {
    margin-bottom: 1em;
}

.margin-bottom2 {
    margin-bottom: 2em;
}

.margin-left1 {
    margin-left: 1em;
}

.margin-left16 {
    margin-left: 1.6em;
}

.margin-right1 {
    margin-right: 1em;
}


/*media query*/
@media only screen and (max-width: 1000px) {
    body {
        --baseFont: 14px;
    }
}




@media only screen and (max-width: 768px) {
    body {
        --baseFont: 12px;
    }

    .page--flex {
        display: block;
        margin: 0 auto;
    }

    .side__30, .side__40, .side__45, .side__50, .side__60 {
        width: 90vw;
    }

    .photo {
        object-fit: contain;
    }

    .photo--no-show {
        display: none;
    }

    .photo--no-show-on-pc {
        display: block;
    }

    .backgroundcolor {
        text-align: center;
    }
}